@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
	text-decoration: none;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
	font-family: 'Poppins', sans-serif;
}
.content-wrapper {
    display: flex;
}
section {
    background: #2a3038;
    /* width: calc(100% - 380px); */
    width: 100%;
    padding-right: 50px;
    margin-top: 97px;
    /* margin-left: 380px; */
    min-height: 100vh;
    padding: 32px 26px;
}

@media (max-width: 768px) {
    section {
        padding: 0;
        margin-top: 59px;
    }
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.18);
  background-color: #2b3139;
  width: 100%;
  height: 97px;
  z-index: 2;
  position: fixed;
}
header .logo {
  display: flex;
  align-items: center;
  padding-left: 50px;
}
header .logo img {
  width: 47px;
  height: 48px;
}
header h1 {
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  text-transform: uppercase;
  margin-left: 13px;
}
header .log-button {
  padding-right: 50px;
}
header .log-button p {
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 4px;
  background-color: #de3143;
  padding: 10px 30px;
  cursor: pointer;
}

@media (max-width: 768px) {
  header {
    height: 59px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.18);
  }
  header .log-button {
    padding-right: 23px;
  }
  header .logo {
    padding-left: 16px;
  }
}
aside {
    width: 380px;
    height: 100vh;
    background-color: #2f3640;
    margin-top: 97px;
    padding-top: 50px;
    position: fixed;
    box-shadow: 5px 0 10px rgba(0, 0, 0, 0.1);
}
nav ul a li {
    color: #fff;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 22px;
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 20px 60px;
    margin: 10px 0;
}
nav ul a svg {
    margin-right: 39px;
}
nav ul a.active li {
    background-color: #e2394a;
}
nav ul a.active svg path {
    fill: #fff;
}
.service-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    padding-left: 22px;
}
.service-header .title {
    color: #fff;
    font-size: 17px;
    font-weight: 600;
}
.service-header input {
    color: #fff;
    font-size: 15px;
    border-radius: 4px;
    border: none;
    background-color: #24282d;
    padding: 10px 19px;
}
.service-header input::-webkit-input-placeholder {
    color: #55585c;
}
.service-header input:-ms-input-placeholder {
    color: #55585c;
}
.service-header input::-ms-input-placeholder {
    color: #55585c;
}
.service-header input::placeholder {
    color: #55585c;
}
.service {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 3px;
    background-color: #313843;
    margin-bottom: 9px;
    padding: 14px 12px 14px 31px;
}
.service .text {
    color: #fff;
    font-size: 14px;
    overflow: hidden;
    max-height: 27px;
    position: relative;
    padding-right: 20px;
}
.service .text::before {
    content: "...";
    position: absolute;
    bottom: 0;
    right: 20px;
}
.service .text::after {
    content: "";
    position: absolute;
    right: 20px;
    width: 1rem;
    height: 1rem;
    background: #313843;
}
.service .service-status p {
    display: inline-block;
    color: #fff;
    font-size: 11px;
    font-weight: 500;
    text-transform: uppercase;
    padding: 8px 16px;
    border-radius: 2px;
}
.service .Stopped p {
    background-color: #e13849;
}
.service .Running p {
    background-color: #36ae72;
}
.service .button {
    color: #f9f9fa;
    font-size: 11px;
    font-weight: 600;
    border-radius: 2px;
    border: 1px solid #4b576a;
    padding: 8px 26px;
    background: transparent;
    text-transform: uppercase;
    margin-left: 13px;
    cursor: pointer;
}
.service .button.special {
    background-color: #3775e0;
}
.service-name {
    width: 40%;
}
.service-status {
    width: 20%;
}
.service-more {
    width: 20%;
}
.service-wrapper {
    width: 66%;
    display: flex;
    align-items: center;
}
.button-wrapper {
    width: 33%;
    display: flex;
    justify-content: flex-end;
}
.warning-text {
    color: #fff;
    font-size: 17px;
    font-weight: 600;
    text-align: center;
}

.service-mobile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #313843;
    margin-bottom: 6px;
    padding: 9px 12px 14px 24px;
}
.service-mobile .service-name-mobile {
    color: #fff;
    font-size: 14px;
}
.service-mobile .service-status-mobile {
    width: 23px;
    height: 23px;
    border-radius: 50%;
}
.service-mobile .service-status-mobile.Stopped {
    background-color: #e13849;
}
.service-mobile .service-status-mobile.Running {
    background-color: #38e18d;
}
.service-mobile .buttons-wrapper-mobile div {
    display: flex;
    justify-content: flex-end;
}
.service-mobile .button {
    color: #f9f9fa;
    font-size: 11px;
    font-weight: 600;
    border-radius: 2px;
    border: 1px solid #4b576a;
    padding: 11px 15px;
    background: transparent;
    text-transform: uppercase;
    margin-left: 9px;
    cursor: pointer;
}
.service-mobile .button.special {
    background-color: #3775e0;
}
.service-mobile .service-name-mobile {
    margin: 0 0 11px 9px;
}
.pagination {
    display: flex;
    justify-content: center;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    margin-top: 30px;
}

.pagination li a {
    color: #fff;
    font-size: 14px;
    margin: 0 5px;
}
.pagination li.active a {
    font-size: 16px;
    font-weight: 700;
}

@media (max-width: 768px) {
    .pagination {
        margin-bottom: 15px;
    }
}

